<template>
<div class='wrapper'>
    <v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
     <div id="infoform">
<el-form id="form1" ref="form1" :inline="true" :model="form1" :rules="rule1" label-width="100px" >
    <el-row style="width:1400px">   
    <el-form-item label="姓名：" prop="pname">
        <el-input v-model="form1.pname" style="width:100%" ></el-input>
     </el-form-item>
   </el-row>
    <el-row style="width:1400px">  
    <el-form-item label="专业编号：" prop="pmajorID">
        <el-input v-model="form1.pmajorID"  style="width:100%"></el-input>
     </el-form-item>
 </el-row>
  <el-row style="width:1400px">  
         <el-form-item label="就职年份：" prop="ptime">
       <el-input v-model="form1.ptime"  style="width:100%"></el-input>
     </el-form-item>
        </el-row>
	 <el-col></el-col>
</el-form>
<el-form id="form2" :inline="true">
<el-form-item>
       <el-button type="primary" @click="modifyForm('form1')">保存</el-button>
                 <el-button type="success" @click="resetForm('form1')">重置</el-button>
                 <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
   
     </el-form>
        </div>
      </div>
    </div>
   </div>
</div>
</template>
    
<script>
  import vHead from '../commons/AdminstratorNav.vue';
  export default {
     components:{
      vHead,
     },
      data: function(){
      return {
          hostUrl:'/teacher',
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
          form1:{
             
              pname: '',
              pmajorID:'',
              ptime: '',
              
          },
          rule1:{
             
          pname: [
            { required: true, message: '输入名字', trigger: 'blur' },
          ],
           pmajorID: [
            { required: true, message: '请输入专业编号', trigger: 'blur' },
            { min: 12, max: 12, message: '长度为12个字符', trigger: 'blur' }
          ],
		  
		   ptime:[
             {  required: true, message: '请选择时间', trigger: 'change' },
         ]
		

          }
      }
    },
        methods: {
       modifyForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      exit(formName) {
        this.$router.push({path: '/adminstrator/teacherInfo/'})
      }
    }
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
}
#infoform{
    padding-top: 60px;
}
#form1{
    width:400px;
    margin:10px auto;
}
#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>


